import React, {Component} from 'react';
import {withRouter} from 'react-router-dom';
import echarts from 'echarts';


class Barchart extends Component {
    state = {
        myChart:null,
        chartId:`FloorSpace${new Date().getTime()}-${Math.random()}`
    };

    componentDidMount() {
        let chartId=this.state.chartId
        let chart = echarts.init(document.getElementById(chartId));  
        this.setState({
            myChart:chart
        },()=>{
            this.setChart()
        })      
        
    }

    setChart = () => {
        let data = this.props.data.RECORDS;
        if (!data || data.length <= 0) {
            return;
        }

        var colors = ['#ad9cff', '#9e89ff', '#856bff', '#00daff', '#00c5ff', '#00a7ff', '#008aff'];
        let xAxisData = data.map(item => item.name);

        var option = {
            tooltip: {
                trigger: 'item',
                formatter: "{a} <br/>{b}: {c} "
            },
            grid: {
                left: 20,
                top: 30,
                bottom: 10,
                right: 20,
                containLabel: true
            },
            xAxis: {
                type: 'category',
                data: xAxisData,
                axisLine: {
                    lineStyle: {
                        color: "#00CFFF",
                        type: "dashed"
                    }
                },
                axisLabel: {
                    color: "#00CFFF",
                    interval: 0
                },
                axisTick: {
                    alignWithLabel: true
                }
            },
            yAxis: {
                type: 'value',
                axisLine: {
                    show: false
                },
                axisLabel: {
                    show: false
                },
                splitLine: {
                    show: false
                }
            },
            color: colors,
            series: [
                {
                    name: '',
                    type: 'bar',
                    barMaxWidth: '20px',
                    itemStyle:{
                        normal:{
                            color:new echarts.graphic.LinearGradient(0, 0, 0, 1, [{
                                offset: 0,
                                color: 'rgba(0,207,255,1)'
                            }, {
                                offset: 0.8,
                                color: 'rgba(0,207,255,0.8)'
                            }, {
                                offset: 1,
                                color: 'rgba(0,207,255,0.19)'
                            }], false)
                        }
                    },
                    label: {
                        show: true,
                        position: "top",
                        color: "#fff",
                        fontSize: 14
                    },
                    data: data
                }
            ]
        };

        this.state.myChart.setOption(option);
    };

    render() {

        return (
            <div id={this.state.chartId} style={{width: '100%', height: '100%'}}></div>
        );
    }
}

export default withRouter(Barchart);