import React, { Component } from 'react'
import { Carousel} from 'antd'
// 学校
import Biyelv from './component/school/Biyelv'
import Biyesheji from './component/school/Biyesheji'
import Dinggaoshixi from './component/school/Dinggaoshixi'
import Jiuyelv from './component/school/Jiuyelv'
import Ruzhibannianmanyidu from './component/school/Ruzhibannianmanyidu'
import Shengjunyiqi from './component/school/Shengjunyiqi'
import Shuangshisuzijiaoshi from './component/school/Shuangshisuzijiaoshi'
import Wenmingbanji from './component/school/Wenmingbanji'
import Wenmingsushe from './component/school/Wenmingsushe'
import Zhuanyezongxueshi from './component/school/Zhuanyezongxueshi'

// 专业
import Ruzhibannianmanyidu2 from './component/major/Ruzhibannianmanyidu'
import Shengjunyiqi2 from './component/major/Shengjunyiqi'


export default class Yujing extends Component {
  render() {
    let autoplaySpeed = 40000;
        const settings = {
            // autoplay: true,
            autoplaySpeed,
            dots: true,
            infinite: false,
            slidesToShow: 1,
            slidesToScroll: 1,
            vertical: true,
            verticalSwiping: true,
            beforeChange: function (currentSlide, nextSlide) {
                // console.log("before change", currentSlide, nextSlide);
            },
            afterChange: (currentSlide) => {
                if (currentSlide === 4) {
                    setTimeout(() => {
                        if (this.slider && this.slider.goTo) {
                            this.slider.goTo(0)
                        }
                    }, autoplaySpeed)
                }
            },
        };
    return (
      <Carousel  ref={slider => (this.slider = slider)} {...settings} >
      {/* 学校 */}
        <div>
            <div style={{ height:'calc(100vh - 74px)',overflowY:'auto'}}>          
                <Biyelv></Biyelv>
                <Biyesheji></Biyesheji>
                <Dinggaoshixi></Dinggaoshixi>
                <Jiuyelv></Jiuyelv>
                <Ruzhibannianmanyidu></Ruzhibannianmanyidu>
                <Shengjunyiqi></Shengjunyiqi>
                <Shuangshisuzijiaoshi></Shuangshisuzijiaoshi>
                <Wenmingbanji></Wenmingbanji>
                <Wenmingsushe></Wenmingsushe>
                <Zhuanyezongxueshi></Zhuanyezongxueshi>
            </div>
        </div>
        {/* 专业 */}
        <div>
            <div style={{ height:'calc(100vh - 74px)',overflowY:'auto'}}>          
                <Ruzhibannianmanyidu2></Ruzhibannianmanyidu2>
                <Shengjunyiqi2></Shengjunyiqi2>
            </div>
        </div>
      </Carousel>
    )
  }
}




